  .footer__column--socials {
    flex-direction: column;
    padding-bottom: 25px;
  }
}

.footer__logo-link {
  align-self: center;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__logo-link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}

.footer__socials {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-self: center;
  justify-content: center;
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  gap: 15px;
}

.footer__socials-link {
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  color: #f8fdff;
  transition: color 0.3s ease, opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__socials-link:hover:not(.focus-visible) {
    opacity: 0.6;
    color: #00b2ff;
  }
}
.footer__socials-link:active {
  opacity: 1;
}

.footer__accordion {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer__accordion {
    flex-direction: column;
  }
}

.footer__sections-wrapper {
  display: grid;
  grid-template-columns: minmax(10px, 1fr) minmax(10px, 1fr);
  gap: 30px;
}
@media (max-width: 1023px) {
  .footer__sections-wrapper {
    grid-template-columns: minmax(10px, 1fr);
    gap: 10px;
  }
}

.footer__sections-wrapper--contacts {
  display: flex;
}

.footer__column h2 {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  margin: 0;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (max-width: 767px) {
  .footer__column h2 {
    width: 100%;
    cursor: pointer;
  }
  .footer__column h2::before, .footer__column h2::after {
    background-color: #c4c4c4;
    content: "";
    position: absolute;
    right: 20px;
    top: 20px;
    width: 1px;
    height: 15px;
    transition: transform 0.3s ease;
  }
  .footer__column h2::before {
    transform: rotate(90deg);
  }
}
@media (max-width: 767px) and (hover: hover) {
  .footer__column h2:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}

@media (max-width: 767px) {
  .is-active h2::after {
    transform: rotate(90deg);
  }
}

.footer__list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
}

@media (max-width: 767px) {
  .footer__list--padding {
    padding-top: 20px;
  }
}

.footer__list-item:not(:last-child) {
  margin-bottom: 10px;
}

.footer__link {
  color: inherit;
  font-family: "PT Mono", "Lucida Console", monospace;
  font-size: 16px;
  line-height: 22px;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 14px;
    line-height: 19px;
  }
}

.modal__content--cb {
  background-color: #00b2ff;
  color: #ffffff;
  max-width: 500px;
}

.modal__close-btn {
  color: #ffffff;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .modal__close-btn:hover:not(.focus-visible) {
    color: #041827;
  }
}

.modal__form h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  margin: 0;
}

.modal__form p {
  font-family: "PT Mono", "Lucida Console", monospace;
  font-size: 16px;
  line-height: 22px;
  margin: 15px 0 0;
}

.modal__form {
  min-width: 420px;
  display: grid;
  grid-template-columns: minmax(10px, 1fr);
}

.modal__inputs-wrapper {
  display: grid;
  grid-template-columns: minmax(10px, 1fr);
  gap: 30px 20px;
  padding: 40px 0 25px;
}

.modal__inputs-wrapper input,
.modal__inputs-wrapper textarea {
  background-color: rgba(4, 20, 39, 0.15);
  border: 2px solid transparent;
  border-radius: 3px;
  padding: 15px;
  font-family: "PT Mono", "Lucida Console", monospace;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  width: 100%;
  transition: border 0.3s ease;
}

.modal__inputs-wrapper input:hover,
.modal__inputs-wrapper textarea:hover {
  border: 2px solid #f2f5f6;
}

.modal__inputs-wrapper textarea {
  padding: 25px 15px;
  min-height: 155px;
}

.modal__inputs-wrapper input::placeholder,
.modal__inputs-wrapper textarea::placeholder {
  font-family: "PT Mono", "Lucida Console", monospace;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}

.modal__submit-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: column;
  gap: 25px;
}

.modal__btn {
  width: 100%;
}

.checkbox--form {
  max-width: 260px;
}

.checkbox {
  display: flex;
  align-self: center;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.checkbox__input {
  appearance: none;
  position: relative;
  min-width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  cursor: inherit;
  transition: opacity 0.3s ease;
}

.checkbox__input::before {
  position: absolute;
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  width: 6px;
  height: 4px;
  transform: rotate(135deg);
  left: 3px;
  top: 3px;
}

.checkbox .checkbox__input:checked::before {
  content: "";
}

.checkbox:hover input,
.checkbox:hover span {
  opacity: 0.6;
}

.checkbox__text {
  font-family: "PT Mono", "Lucida Console", monospace;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.8;
  transition: opacity 0.3s ease;
}

@media (max-width: 768px) {
  .visibility-tablet,
.visibility-desktop {
    display: none;
  }
}
@media (min-width: 768px) {
  .visibility-mobile,
.visibility-desktop {
    display: none;
  }
  .visibility-tablet {
    display: block;
  }
}
@media (min-width: 1023px) {
  .visibility-mobile,
.visibility-tablet {
    display: none;
  }
  .visibility-desktop {
    display: block;
  }
}
